Drupal.behaviors.scrolltop = {
    attach: function(context, settings) {
        $(window).on('load',function() {
            $('body').append('<div id="topScroll" style="position: fixed; z-index: 999; right:0; bottom:0;"><button class="button" type="submit" style="border: none; border-radius: 25%; background:none; margin-right: 25px; margin-bottom: 25px; cursor: pointer;"><img style="margin-right: 25px; margin-bottom: 25px;"; src="images/icon_gototop.png"></div>');
            $('.button').click(function() {
                $("html, body").animate({scrollTop: 0});
                console.log("Button Pressed");
            });
            $('#topScroll').hide();
        })
        // Conditions for hiding and showing button
        $(window).scroll(function() {
        if($(window).scrollTop() < 200){
            $('#topScroll').hide(600);
            console.log("Button Hidden");
        }
        else if($(window).scrollTop()>200 && $('#topScroll')[0]) {
            $('#topScroll').show(600);
            console.log("Button Shown")
    }});
    }
};